
.container {
    place-items: center;
    display: flex;
    justify-content: space-around;
    margin: auto;
    width: 100%;
    
    
}

.button1 {
    position:relative;
    display:flexbox;
    background-color: red;
    width: 14vw;
    height: 14vw;
    border-radius: 50%;
    border: solid .3rem #000000;
    font-size: 3rem;
    animation: moving 5s linear infinite;
}
@keyframes moving{
    0%{
        top:0;
    }
    25%{
        top: 5rem;
        background:cyan;
    }
    50%{
        top: 0;
    }
    75%{
        top: 5rem;
    }
    100%{
        top: 0;
    }
}

